import React, { Component } from 'react';
import HeaderBg from '../HeaderBg/HeaderBg';
import Search from '../Search/Search';
import PropTypes from 'prop-types';
import './style.css';

class Header extends Component {
  constructor(props) {
    super(props);
    this.handleExecSearch = this.handleExecSearch.bind(this)
  }

  render() {
    return (
      <div className="header-con">
        <div className="header-con-bg">
          <HeaderBg />
        </div>
        <div className="header-con-items">
          <div className="header-con-items-title">
            Demo Hub
          </div>
          <div className="header-con-items-search">
            <Search
              handleExecSearch={this.handleExecSearch}
            />
          </div>
        </div>
      </div>
    );
  }

  handleExecSearch(value) {
    this.props.handleExecSearch(value);
  }
};

Header.propTypes = {
  handleExecSearch: PropTypes.func
}

export default Header;
