import React, { Component } from 'react';
import Card from '../Card/Card';
import store from '../../store';
import './style.css';

class Body extends Component {
  constructor(props) {
    super(props);
    this.state = store.getState()
  }

  componentDidMount() {
    console.log(this.state);
  }

  render() {
    return (
      <div className="body-con">
        <div className="body-con-items">
          {
            this.state.allDemo.map((item, index) => (
              <Card
                key={index}
                item={item}
              />
            ))
          }
        </div>
      </div>
    );
  }
};

export default Body;
