import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './style.css';

class Search extends Component {
  constructor(props) {
    super(props);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleInputBlur = this.handleInputBlur.bind(this);
    this.handleInputFocus = this.handleInputFocus.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.state = {
      inputValue: '',
      inputFocus: false
    }
  }

  render() {
    const focusColor = {
      color: '#39c'
    };
    const blurColor = {
      color: '#999'
    };
    return (
      <div className="search-con">
        <div className="search-con-wrap">
          <div className="search-con-wrap-left">
            <i 
              className="icomoon-search search-icon"
              style={this.state.inputFocus ? focusColor : blurColor}
            ></i>
            <div className="search-con-wrap-left-search">

              <input
                type="text"
                value={this.state.inputValue}
                onChange={this.handleInputChange}
                onFocus={this.handleInputFocus}
                onBlur={this.handleInputBlur}
                placeholder="请输入要搜索的标签..."
              />
            </div>
          </div>
          <div 
            className="search-con-wrap-right"
            onClick={this.handleClick}
          >
            Find Your Dream
          </div>
        </div>
      </div>
    );
  }

  handleInputChange(e) {
    this.setState({
      inputValue: e.target.value
    });
  }

  handleInputFocus() {
    this.setState({
      inputFocus: true
    });
  }

  handleInputBlur() {
    this.setState({
      inputFocus: false
    });
  }

  handleClick() {
    this.props.handleExecSearch(this.state.inputValue);
  }
};

Search.propTypes = {
  handleExecSearch: PropTypes.func
}

export default Search;
