import React, { Component } from 'react';
import PropTypes from 'prop-types';
import logo from '../../assets/logo.jpeg';
import './style.css';

class Card extends Component {
  constructor(props) {
    super(props);
    this.getTags = this.getTags.bind(this);
    this.state = {
      tags: ''
    }
  }

  componentDidMount() {
    this.getTags();
  }

  render() {
    return (
      <div className="card">
        <div className="card-top">
          <div className="card-top-img">
            <img src={logo} alt=""/>
          </div>
        </div>
        <div className="card-body">
          <div className="card-body-title">
            <a>{this.props.item.title}</a>
          </div>
          <div className="card-body-desc">
            {this.props.item.description}
          </div>
          <div className="card-body-tag">
            <div className="card-body-tag-icon">
              <i className="icomoon-list"></i>
            </div>
            <div className="card-body-tag-items">
              {this.state.tags}
            </div>
          </div>
        </div>
        <div className="card-bottom">
          
        </div>
      </div>
    );
  }

  getTags() {
    console.log(this.props.item)
    let str = ''
    this.props.item.tags.forEach((item, index) => {
      if (index === this.props.item.tags.length - 1) {
        str += item
      } else {
        str += `${item}, `
      }
    })
    this.setState({
      tags: str
    });
  }
};

Card.propTypes = {
  item: PropTypes.object
}

export default Card;
